#components-launch {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  z-index: 9999;
  visibility: visible;
  opacity: 100;
  background-color: #20232a;
  pointer-events: none; }
  #components-launch.disabled {
    visibility: hidden;
    opacity: 0;
    pointer-events: auto;
    -webkit-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s; }
  #components-launch .launch p {
    color: #fff;
    font-size: 20px;
    line-height: 2; }
  #components-launch .launch img {
    margin-top: 20%;
    max-width: 640px; }
